@mixin frowEqualHeight($justify){
	display:flex;
	flex-flow:row;
	justify-content: $justify;
	align-items:stretch;
}
@mixin frowContentCentered($justify){
	display:flex;
	flex-flow:row;
	justify-content: $justify;
	align-items:center;
}

header.site-header{

	*{
		color:white;
	}
}
main{
	padding:1rem;
}


article{
	padding:2rem;
	margin:1rem 0;
	header{
		width:100%;
		margin-bottom: .5rem;
	}
	.article-body{
		margin-bottom: .5rem;
	}
	footer{
		width:100%;
		border-top:solid 1px grey;
	}
}

footer.site-footer{
	width:100%;
	padding:1rem;
	background:#ddd;
}